import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {ProductService, ProductModel} from './product.service';


@Component({
  moduleId: module.id,
//   styleUrls: [`monster.css`],
  // selector: 'monster-list',
  template: `
    <section *ngIf="product">
      <h2>Monster {{product.name}}</h2>
      <!--<img [src]="product.getImgUrl()" >-->

    </section>
  `
})
export class ProductComponent implements OnInit {

  private product : ProductModel;

  constructor(  private route: ActivatedRoute,
                private productService : ProductService
  ) { }

  ngOnInit() {
   this.route.params.subscribe(params => {
    //  console.log('Params are: ', params);
     const id = +params['id'];
     const prmProduct = this.productService.get(id);
     prmProduct.then((product: ProductModel) => {
       this.product = product;
     });
   });
  }



}
